@import "normalizez.scss";
@import "resets.scss";

@font-face {
    font-family: Display;
    src: url(../fonts/PlayfairDisplay-Regular.ttf); }
@font-face {
    font-family: Display-ExtraBold;
    src: url(../fonts/PlayfairDisplay-ExtraBold.ttf); }
@font-face {
    font-family: Display-Italic;
    src: url(../fonts/PlayfairDisplay-Italic.ttf); }
@font-face {
    font-family: OpenSans-Regular;
    src: url(../fonts/OpenSans-Regular.ttf); }
@font-face {
    font-family: OpenSans-Light;
    src: url(../fonts/OpenSans-Light.ttf); }

$main-color1: #353e47;
$main-color2: #cdba6d;
$main-color3: #323a43;
$main-color4: #2e353d;
$main-color5: #5c6f87;

.main-section {
    max-width: 100%;
    min-height: 800px;
    &__nav-menu {
        display: flex;
        align-items: center;
        height: 60px;
        background-color: $main-color1; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        margin-right: 250px;
        padding: 20px;
        color: $main-color2;
        font-size: 15px;
        font-family: OpenSans-Regular;
        a {
            text-decoration: none;
            color: $main-color2; }
        &:last-child {
            margin-right: 0px; } } }

.first-section {
    max-height: 700px;
    background-image: url(../image/Component3.png);
    background-size: cover;
    background-repeat: no-repeat; }

.main-photo {
    position: relative;
    bottom: 80px;
    display: flex;
    justify-content: flex-end;

    img {
        max-height: 600px; } }

.title-text {
    position: relative;
    color: $main-color2;
    font-size: 60px;
    top: 250px;
    display: flex;
    justify-content: center;
    text-align: center;
    line-height: 65px;
    font-family: Display-ExtraBold; }
.title-text-two {
    position: relative;
    color: white;
    font-size: 20px;
    top: 270px;
    display: flex;
    justify-content: center;
    text-align: center;
    line-height: 25px;
    font-family: OpenSans-Light; }

.second-section {
    min-height: 700px; }

.second-section__card-box {
    min-width: 100%; }

.box-size {
    height: 290px;
    width: 100%;
    background: $main-color1;
    margin-bottom: 30px;
    padding: 10px; }

.card-item__img {
    margin: auto;
    display: flex;
    justify-content: center;
    padding-top: 40px;
    height: 40px;
    width: 40px; }


.card-item__text-title {
    color: white;
    text-align: center;
    font-size: 20px;
    padding-top: 30px;
    padding-bottom: 20px;
    line-height: 25px;
    font-family: Display-ExtraBold; }
.card-item__text-title2 {
    color: white;
    text-align: center;
    font-size: 20px;
    padding-top: 30px;
    padding-bottom: 20px;
    line-height: 25px;
    font-family: OpenSans-Regular; }


.card-item__text2 {
    color: white;
    text-align: center;
    font-size: 18px;
    padding-top: 10px;
    line-height: 25px;
    font-family: OpenSans-Regular; }
.title__hr {
    width: 100px;
    border: 1px solid;
    color: $main-color2; }

.title__hr-title {
    width: 100px;
    border: 1px solid;
    color: $main-color2; }

.second-section__card-box {
    display: inline-flex; }

.card-color-3 {
    background: $main-color3; }
.card-color-4 {
    background: $main-color4; }

.third-section {
    max-width: 100%;
    height: 500px;
    background-image: url(../image/main-photo3.png);
    background-size: contain;
    background-repeat: no-repeat; }

.title-about-me {
    position: relative;
    text-align: center;
    font-size: 40px;
    margin-bottom: 40px;
    color: $main-color2;
    font-family: Display; }


.title__hr1 {
    position: relative;
    width: 100px;
    border: 1px solid;
    color: $main-color2;
    right: 30px; }

.about-me {
    max-width: 800px;
    margin: auto;
    position: relative;
    text-align: center;
    font-size: 20px;
    line-height: 40px;
    color: $main-color5;
    font-family: Display; }

.fourth-section {

    height: 700px;
    background-color: $main-color3;
    margin-bottom: 50px; }

.specialization {
    position: relative;
    top: 50px;
    text-align: center;
    font-size: 40px;
    color: $main-color2;
    font-family: Display-ExtraBold; }


.title__hr-specialization {
    position: relative;
    width: 100px;
    border: 1px solid;
    color: $main-color2;
    top: 70px;
    margin-bottom: 120px; }
.fourth__card-box {
    display: inline-flex; }


.card-color-5 {
    background: $main-color3; }
.card-color-6 {
    background: $main-color3; }
.card-color-7 {
    background: $main-color3; }

.fifth-section {

    min-height: 600px;
    background-image: url(../image/background3.png);
    background-size: cover;
    background-repeat: no-repeat;
    margin-bottom: 30px; }

.advantages {
    position: relative;
    top: 30px;
    text-align: center;
    font-size: 50px;
    color: $main-color2;
    margin-bottom: 70px;
    font-family: Display; }
.box-size2 {
    position: relative;
    height: 290px;
    width: 100%;
    margin-right: 30px;
    background: none;
    padding: 10px;
    margin-bottom: 20px;
    &:last-child {
        margin-right: 0; } }

.fifth__card-box {
    display: inline-flex; }

.footer {
    height: 400px;
    background-color: $main-color1; }
.footer__list {
    color: $main-color2;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 200px;
    align-items: center;
    font-size: 18px;
    background-color: $main-color1;
    font-family: OpenSans-Regular; }
.list-item {
    margin-top: 30px; }

.footer-text {
    position: relative;
    align-items: center;
    display: inline;
    bottom: 1px;
    left: 40px;
    font-size: 18px;
    font-family: OpenSans-Light;
    color: white;
    line-height: 30px; }

